.schedule{
  &-block{
    &__header{
      font-weight: 600;
      font-size: 25px;

      color:$monoBlack;

      letter-spacing: -0.2px;

      display: flex;
      align-items: center;
      span{
        margin-left: 5px;
      }
      &::before{
        content: '';
        width: 36px;
        height: 36px;
        display: block;

        background: url("../img/module-arrow.svg") center center/contain no-repeat;

        margin-right: 20px;

        transform: rotate(-90deg);

        transition: .3s all;
      }
      &.active{
        &::before{
          transform: rotate(0);
        }
      }
    }
    &__content{
      margin-top: 40px;
    }
  }
  &-grid{
    display: grid;
    grid-template-columns: repeat(4,minmax(0,1fr));
    grid-auto-rows: 186px;
    gap: 30px 40px;
  }
  &-wrapper{
    &__content{
      margin-top: 45px;
    }
    &__block + &__block{
      margin-top: 45px;
    }
  }
  &-item{
    height: 100%;
    &:hover{
      .schedule-item__footer{
        opacity: 1;
      }
    }
    &__wrapper{
      height: 100%;
      padding: 20px;

      background: #FFFFFF;
      box-shadow: 0px 4px 45px rgba(0, 0, 0, 0.1);
      border-radius: 25px;

      display: flex;
      flex-direction: column;
    }
    &__header{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__time{
      font-size: 16px;
      color: #95929C;
    }
    &__content{
      margin-top: 15px;
    }
    &__title{
      font-size: 20px;
      font-weight: 600;
      letter-spacing: -0.2px;
      color: #020203;
    }
    &__subtitle{
      margin-top: 15px;

      font-size: 16px;
      color: #95929C;
    }
    &__footer{
      margin-top: auto;

      opacity: 0;

      transition: .3s opacity;
    }
    &__link{
      font-weight: 600;
      font-size: 17px;

      color:$accent;
    }
  }
  &-popup{
    &__list{
      &-item + &-item{
        margin-top: 15px;
      }
    }
  }
}

@media screen and (max-width: 1280px) {
  .schedule{
    &-grid{
      grid-template-columns: repeat(2,minmax(0,270px));
    }
  }
}

@media screen and (max-width: 600px) {
  .schedule{
    &-grid{
      grid-template-columns: minmax(0,1fr);
    }
    &-block{
      &-wrapper{
        &__content{
          margin-top: 30px;
        }
        &__block + &__block{
          margin-top: 30px;
        }
      }
    }
  }
}